.jp-state-playing {
  .jp-interface {
    .jp-controls {
      button.jp-play {
        &:hover {
          background: transparent;
        }
        &:focus {
          background: transparent;
        }
        &:active {
          background: transparent;
        }
      }
      button {
        .pause {
          opacity: 1;
          visibility: visible;
        }
        .play {
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }
}
.jp-state-muted {
  .jp-interface {
    .jp-volume-controls {
      button.jp-mute {
        background: transparent;
      }
      button {
        .off {
          opacity: 1;
          visibility: visible;
        }
        .down {
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }
}
.jp-state-looped {
  .jp-toggles {
    button.jp-repeat {
      color: #0088cc;
      &:hover {
        color: #fff;
      }
    }
  }
}
.jp-interface {
  background: rgba(0, 0, 0, 0.8);
  padding: 15px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: auto;
  margin-top: 0;
  &:before {
    content: "";
    display: table;
    clear: both;
  }
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}
.jp-controls {
  width: auto;
  padding: 0;
  float: left;
  button {
    color: #eeeeee;
    background: transparent;
    font-size: 14px;
    float: left;
    display: inline-block;
    width: 20px;
    height: 30px;
    position: relative;
    margin: 0 10px;
    padding: 0;
    border: none;
    -webkit-transition: all 0.2s ease-in-out 0s;
    -moz-transition: all 0.2s ease-in-out 0s;
    -ms-transition: all 0.2s ease-in-out 0s;
    -o-transition: all 0.2s ease-in-out 0s;
    transition: all 0.2s ease-in-out 0s;
    &:hover {
      outline: none;
      background: transparent;
      color: #0088cc;
    }
    &:focus {
      outline: none;
      background: transparent;
    }
    &:active {
      outline: none;
      background: transparent;
    }
    i {
      margin-left: 1px;
    }
    .play {
      position: absolute;
      width: auto;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -o-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      opacity: 1;
      visibility: visible;
    }
    .pause {
      position: absolute;
      width: auto;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -o-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      opacity: 0;
      visibility: hidden;
    }
  }
}
.jp-progress {
  position: absolute;
  top: -4px;
  left: 0;
  width: 100%;
  height: 4px;
  * {
    height: 100%;
  }
}
