}
.jp-volume-controls {
  width: auto;
  float: right;
  button {
    display: inline-block;
    position: relative;
    border: none;
    cursor: pointer;
    background: transparent;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    float: left;
    color: #fff;
    padding: 0;
    -webkit-transition: all 0.2s ease-in-out 0s;
    -moz-transition: all 0.2s ease-in-out 0s;
    -ms-transition: all 0.2s ease-in-out 0s;
    -o-transition: all 0.2s ease-in-out 0s;
    transition: all 0.2s ease-in-out 0s;
    &:hover {
      outline: none;
      background: transparent;
      color: #0088cc;
    }
    &:focus {
      outline: none;
      background: transparent;
      color: #0088cc;
    }
    &:active {
      outline: none;
      background: transparent;
      color: #0088cc;
    }
    .off {
      position: absolute;
      width: auto;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -o-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      -webkit-transition: all 0.2s ease-in-out 0s;
      -moz-transition: all 0.2s ease-in-out 0s;
      -ms-transition: all 0.2s ease-in-out 0s;
      -o-transition: all 0.2s ease-in-out 0s;
      transition: all 0.2s ease-in-out 0s;
      opacity: 0;
      visibility: hidden;
      margin-left: -2px;
    }
    .down {
      position: absolute;
      width: auto;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -o-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      -webkit-transition: all 0.2s ease-in-out 0s;
      -moz-transition: all 0.2s ease-in-out 0s;
      -ms-transition: all 0.2s ease-in-out 0s;
      -o-transition: all 0.2s ease-in-out 0s;
      transition: all 0.2s ease-in-out 0s;
      opacity: 1;
      visibility: visible;
    }
  }
  .jp-volume-bar {
    position: relative;
    overflow: hidden;
    background: #555;
    width: 60px;
    height: 2px;
    cursor: pointer;
    display: inherit;
    float: left;
    margin: 14px 0;
    .jp-volume-bar-value {
      position: absolute;
      height: 100%;
      left: 0;
      top: 0;
      background: #0088cc;
    }
  }
}
